.account-lang__header {
  box-sizing: border-box;
  padding: 14px;
  height: 50px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9; }
  .account-lang__header img {
    position: absolute;
    top: 0px;
    left: 0px;
