.switch-box {
  position: fixed;
  z-index: 10000;
  width: 100vw;
  height: 100%;
  top: 0;
  left: 0; }
  .switch-box .black_box {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 101;
    background: rgba(9, 14, 25, 0.3); }
  .switch-box .kuang {
    position: absolute;
